/* profile */

.profile {
    --light-color: #black;
    --dark-color: #f3f3f4;
    --dark-background-color: #0a0809;
    --activity-color: #;
    /* display  */
    --display-background-color: #d9eff7;
    --field-name: #1a356b;
}

.profile {
    height: 100vh;
    min-width: 100vw;
    display: grid;
    grid-template-columns: 3fr 1fr;
}


/* .profile .activity {
    background-color: var(--dark-background-color);
} */

.profile .display {
    display: grid;
    grid-template-rows: 0.6fr 1fr;
    grid-template-columns: minmax(0.25vw, auto);
    background-color: var(--display-background-color);
}

.profile .display .profile-image {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--display-background-color);
}


/* .profile .display .profile-image::after {
    content: "";
    height: 2px;
    width: 100%;
    background-color: #f3f3f4;
} */

.profile .display .profile-image img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
}

.profile .display .details {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column wrap;
}


/* .profile .display .details h3 {
    cursor: pointer;
    margin: 0.5rem 0 0.5rem 0.5rem;
} */

.profile .display .details .data-field {
    display: flex;
    flex-flow: column nowrap;
    padding: 0 0 0 1rem;
    background-color: var(--display-background-color);
    padding-bottom: 0.3rem;
}

.profile .display .details .data-field .field-name {
    font-size: 1.4rem;
    color: var(--field-name);
    background-color: var(--display-background-color);
}

.profile .display .details .data-field .field-data {
    display: flex;
    flex-flow: row wrap;
    width: 80%;
    justify-content: space-between;
    background-color: var(--display-background-color);
}

.profile .display .details .data-field .field-data p {
    font-size: 1.1rem;
    background-color: var(--display-background-color);
}

.profile .display .details .data-field .field-data p::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: black;
    display: block;
}

.profile .display .details .data-field .field-data .edit-icon {
    color: green;
    cursor: pointer;
    background-color: var(--display-background-color);
}