/* edit-profile */

.edit-profile {
  height: 100vh;
  width: 100vw;
  background-color: gray;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 2;
}

.edit-profile .header {
  display: flex;
  justify-content: space-between;
}

.edit-profile .header form {
  display: flex;
  
}
